#photo {
  width: 33.3%;
}

#photo img{
  border-style: solid;
  background-color: darkturquoise;
  padding: 30px;
}

#bIntro {
  width: 66.6%;
  padding: 20px 20px;
  font-size: 30px;
}

#home {
  display: flex;
  margin-top: 30px;
}

@media (max-width:768px){
  #photo {
    width: 100%;
  }

  #bIntro {
    width: 100%;
  }

  #home {
    display: block;
  }
}
